<template>
  <div>
    <Table border :columns="columns" :data="orderInfo.data" size="large" no-data-text="你还有订单，快点去购物吧"></Table>
    <div class="page-size">
      <Page :current="orderInfo.current_page" :total="orderInfo.total"></Page>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import { getOrderLists, refund, confirmOrder } from '../../api/index';
export default {
  name: 'MyOrder',
  mounted () {
    getOrderLists({user_id: this.userInfo.user_id}, this.userInfo.token).then(
      res => {
        if (res.code === 1) {
          this.SET_ORDERINFO(res.data);
        }
      }
    );
  },
  computed: {
    ...mapState(['userInfo', 'orderInfo'])
  },
  methods: {
    ...mapMutations(['SET_ORDERINFO']),
    goToPay (index) {
      this.$router.push({path: '/order', query: { orderId: this.orderInfo.data[index].order_no }});
    },
    reFound (index) {
      refund({user_id: this.userInfo.user_id, order_no: this.orderInfo.data[index].order_no});
    },
    Confirm (index) {
      confirmOrder({order_no: this.orderInfo.data[index].order_no}, this.userInfo.token);
      getOrderLists({user_id: this.userInfo.user_id}, this.userInfo.token).then(
        res => {
          if (res.code === 1) {
            this.SET_ORDERINFO(res.data);
          }
        }
      );
    }
  },
  data () {
    return {
      columns: [
        {
          title: '订单号',
          key: 'order_no',
          width: 180,
          align: 'center'
        },
        {
          title: '备注',
          key: 'remark',
          align: 'center'
        },
        {
          title: '价格',
          width: 98,
          key: 'goods_amount',
          align: 'center'
        },
        {
          title: '快递方式',
          width: 108,
          key: 'remark',
          render: (h, params) => {
            return h('span', '默认');
          },
          align: 'center'
        },
        {
          title: '创建时间',
          width: 120,
          key: 'create_time',
          render: (h, params) => {
            return h('i-time', {props: { time: params.row.create_time }}, params.row.create_time);
          },
          align: 'center'
        },
        {
          title: '状态',
          width: 120,
          key: 'order_status',
          render: (h, params) => {
            let status = '默认';
            if (params.row.order_status === 0) {
              status = '未支付';
            } else if (params.row.order_status === 1) {
              status = '已支付';
            } else if (params.row.order_status === 2) {
              status = '已确认收货';
            }
            return h('span', status);
          },
          align: 'center'
        },
        {
          title: '操作',
          key: 'action',
          width: 300,
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('Button', {
                props: {
                  type: 'success',
                  size: 'small'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    if (params.row.order_status !== 0) {
                      this.$Message.error('该订单您已支付');
                    } else {
                      this.goToPay(params.index);
                    }
                  }
                }
              }, '查看'),
              h('Button', {
                props: {
                  type: 'primary',
                  size: 'small'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    if (params.row.order_status !== 0) {
                      this.$Message.error('该订单您已支付');
                    } else {
                      this.goToPay(params.index);
                    }
                  }
                }
              }, '支付'),
              h('Button', {
                props: {
                  type: 'error',
                  size: 'small'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    if (params.row.order_status <= 0) {
                      this.$Message.error('该订单您还未支付');
                    } else {
                      this.reFound(params.index);
                    }
                  }
                }
              }, '申请退款'),
              h('Button', {
                props: {
                  type: 'warning',
                  size: 'small'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    if (params.row.order_status <= 0) {
                      this.$Message.error('该订单您还未支付');
                    } else {
                      this.Confirm(params.index);
                    }
                  }
                }
              }, '确认收货')
            ]);
          }
        }
      ]
    };
  }
};
</script>

<style scoped>
.page-size {
  margin: 15px 0px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
</style>



